import React, {useEffect, useRef, useState} from 'react'
import FullCalendar from '@fullcalendar/react' // must go before plugins
import dayGridPlugin from '@fullcalendar/daygrid'
import interactionPlugin from '@fullcalendar/interaction';
import {useHttp} from "../../hooks/http.hook";
import { ToastContainer, toast } from 'react-toastify';
//import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import {Hearts} from "react-loader-spinner"
import Modal from 'react-modal';
import Select from 'react-select'
import './index.css'

let arrForCalendar=[]
let options = []

function Calendar(){
    const [addestUser, setAddestUser] = useState(null)
    const [test, setTest] = useState([])
    const [modalIsOpen, setIsOpen] = useState(false);
    const [allStaff, setAllStaff] = useState(null)
    const {request, loading} = useHttp()
    const [arrFC, setArrFC] = useState()
    const [usersFromServer, setUsersFromServer] = useState(null)

    function closeModal() {
        setIsOpen(false);
    }

    useEffect(() => {
        async function fetchContracts(){
            const datafromserver = await request('/contracts', 'POST') //        
            const users = await request('/allstaff','POST') //        
            setUsersFromServer(users)

            datafromserver.map(e => {
                let obj = {
                    title: e.company,
                    start: (e.start).split('T')[0],
                    workers: e.workers,
                    dogovor_id:e._id
                }
                arrForCalendar.push(obj)
            })
            setArrFC(arrForCalendar)
            arrForCalendar=[]
        }
        fetchContracts()
    },[])
    useEffect(() => {
        async function fetchStaff(){
            const staff = await request('/allstaff', 'POST') //        
            setAllStaff(staff)
        }
        fetchStaff()
    },[])
    useEffect( () => {
        allStaff==null
            ? console.log('allStaff  : ')
            :updateOptions()
    },[allStaff])

    function updateOptions(){
        options=[]
        allStaff.map(e=>{
            let obj={
                worker_name: e.name,
                worker_id:e._id,
                value:e.name,
                label:e.name,
            }
            options.push(obj)
        })
    }
    const setDataForModalByOk = () => {
        setTest([...test,addestUser])
    }

    const changeHandlerWorkers = event => {
        setAddestUser(null)
        const isWorkerAlreadyInside = test.filter(e=>e.worker_id===event.worker_id)
        if(isWorkerAlreadyInside.length>0){
            toast(`${event.value}      !`)
        } else {
            const currentEvent = event
            currentEvent.work_time = 8
            setAddestUser(currentEvent)
        }
    }
    const changeHandlerTime = event => {
        const currentWorker_id = event.target.closest('tr').childNodes[0].childNodes[3].textContent
        test.map(e=>{
            if (e.worker_id && e.worker_id===currentWorker_id) {
                e['work_time'] = event.target.value
            }
        })
        console.log(test)
        //console.log('current worker id: ',currentWorker_id)
    }

    const handleEventClick = async info => {
        const arrayWorkersOfCurrentDogovor = []
        const currentDogovor_id = info.el.fcSeg.eventRange.def.extendedProps.dogovor_id
        const currentDogovor_date = info.event.startStr

        const obj_dogovor_id = { dogovor_id: info.el.fcSeg.eventRange.def.extendedProps.dogovor_id }
        const obj_dogovor_date = { dogovor_date: info.event.startStr }

        arrayWorkersOfCurrentDogovor.push(obj_dogovor_id)
        arrayWorkersOfCurrentDogovor.push(obj_dogovor_date)

        const arrayOfWorkersID = info.el.fcSeg.eventRange.def.extendedProps.workers
        const result = await request('/getCurrentWorkTime', 'POST', arrayOfWorkersID) //        

        arrayOfWorkersID.map(e => {
            for(let oneWA of result) {
                if(result.includes(null)){
                    console.log('  wa  null.    wa   .')
                }
            if (oneWA.worker_id === e && oneWA.dogovor_id === currentDogovor_id && (oneWA.dogovor_date).split('T')[0] === currentDogovor_date) {
                usersFromServer.map(el => {
                        if (el._id === e) {
                            const obj = {
                                worker_name: el.name,
                                worker_id: el._id,
                                work_time: oneWA.hours
                            }
                            arrayWorkersOfCurrentDogovor.push(obj)
                        }
                    })
                }
            }
        })

        setTest(arrayWorkersOfCurrentDogovor)
        setIsOpen(true);
    }
    const removeWorker = event => {
        let removedWorker = null
        const removedWorker_id = event.target.closest('td').childNodes[3].textContent
        test.map(e=>{
            if(e.worker_id && e.worker_id===removedWorker_id) removedWorker = e
        })
        if(removedWorker){
            setTest(test.filter(e => e !== removedWorker))
        }
    }
    const checkTest = async () => {
        console.log('test: ',test)
        const res = await request('/updateWorkersOfDogovor','POST', test) //        
        console.log('  : ',res)
    }

    return(
            <div className="mainBox">
                <div className="boxFC">
                    {loading? <Hearts
                        className="loaderBoxFc"
                        type="Puff"
                        color="#00BFFF"
                        height={45}
                        width={45}
                    />: ''}
                    <FullCalendar
                        headerToolbar = {{
                            start: 'today prev,next',
                            center: 'title',
                            end: 'dayGridMonth,dayGridWeek,dayGridDay'
                        }}
                        plugins={[dayGridPlugin, interactionPlugin]}
                        initialView="dayGridMonth"
                        locale="ru"
                        dayMaxEvents="true"
                        events={arrFC}
                        editable="true"
                        eventDrop={async info => {
                            const obj = {
                                company: info.event.title,
                                start: info.event.startStr
                            }
                            if(!window.confirm(" ?")){
                                // eslint-disable-next-line no-unused-expressions
                                info.revert()
                            }else {
                                console.log(obj)
                                await request('/setnewstart', 'POST',obj)
                                    .then(
                                        toast(` !`, {
                                            position: "top-right",
                                            autoClose: 5000,
                                            hideProgressBar: false,
                                            closeOnClick: true,
                                            pauseOnHover: true,
                                            draggable: true,
                                            progress: undefined,
                                        })
                                    )
                            }
                        }}
                        eventClick={handleEventClick}
                        droppable="true"
                        buttonText={{
                                today: "",
                                month: "",
                                week: "",
                                day: ""
                        }}
                        firstDay={1}

                    />
                </div>
                <Modal
                    className="modalGrafik"
                    overlayClassName="Overlay"
                    isOpen={modalIsOpen}
                    onRequestClose={closeModal}
                >
                    <div className="mainModalDiv">
                        <div>/ </div>
                    </div>
                    {
                         <table id="tableAtModal_GraficPage">
                            <thead>
                                <tr>
                                    <th id="firstColumnAtModal"></th>
                                    <th id="secondColumnAtModal"> , </th>
                                    <th></th>
                                </tr>
                            </thead>
                             <tr className=" selectWorkers">
                                 <Select onChange={changeHandlerWorkers} options={options} id="selectionModal"/>
                                 <button onClick={setDataForModalByOk} className="btn btn-success buttonAddWorkerInsideModal"></button>
                             </tr>
                             {test.length>0?
                                 test.map(e=>{
                                 if(e.worker_name){
                                     return(
                                         <tr className="canBeRemovedByUser">
                                             <td>
                                                 <div className="rigtherNameModal float-start">{e.worker_name}</div> <div onClick={removeWorker} className="float-end buttonRemovedWorkerInsideModal"></div>
                                                 <div className="d-none">{e.worker_id}</div>
                                             </td>
                                             <td onChange={changeHandlerTime} className="tdWorkTime"><input type="number" placeholder={e.work_time}/></td>
                                         </tr>
                                     )
                                 }
                                 })
                                 : '...     ...    -   - ...'
                             }
                        </table>

                    }
                    <div className="btnApproveWorkersAtModal">
                        <button onClick={checkTest} className="btn btn-success"></button>
                        <button onClick={closeModal} className="btn btn-danger float-end"></button>
                    </div>

                </Modal>
                <ToastContainer/>
            </div>
    )
}
export default Calendar