import React, {useContext, useEffect, useState} from "react";
import './auth.css'
import {toast} from "react-toastify";
import {useHttp} from "../../hooks/http.hook";
import {AuthContext} from "../../context/AuthContext";
import {Link} from "react-router-dom";

function Auth(){
    const auth = useContext(AuthContext)
    const {error, request, clearError} = useHttp()
    const [data,setData] = useState({
        email:null,
        password:null
    })

    useEffect(()=>{
        if(error===null){
        }else{
            toast(` Wow so easy! ${error}`, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
        clearError()
    },[error, clearError])

    const changeHandler = event => {
        setData({...data, [event.target.name]: event.target.value})
    }
    const loginHandler = async () => {
        try {
            const datafromserver = await request('/api/auth/login','POST',{...data}) //        
            toast(` ${datafromserver.message}`, {
                position: "top-right",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            auth.login(datafromserver.token, datafromserver.userId)
        } catch (e){}
    }

    return(
        <div>
            <div className="mainDiv ">
                <div className="myform col-md-6">
                    <h2></h2>
                    <div className="mb-3 ">
                        <label htmlFor="exampleInputEmail1" className="form-label">Email address</label>
                        <input onChange={changeHandler} name="email" type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp"/>
                        <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
                    </div>
                    <div className="mb-3">
                        <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
                        <input onChange={changeHandler} name="password" type="password" className="form-control" id="exampleInputPassword1"/>
                    </div>
                    <div className="mb-3 form-check">
                        <input className="checkqadro" type="checkbox" id="exampleCheck1"/>
                        <label className="form-check-label" htmlFor="exampleCheck1"> </label>
                    </div>
                    <button onClick={loginHandler} className="btn btn-primary"></button>
                    <Link to="/register">
                        <button className="btn btn-primary"></button>
                    </Link>
                </div>

            </div>
        </div>
    )
}
export default Auth