import React, {useContext, useEffect, useState} from 'react'
import {useHttp} from "../../hooks/http.hook";
import {AuthContext} from "../../context/AuthContext";
const moment = require('moment')
require ('./finance.css')

function Finance(){
    let arr = []
    const {request, loading} = useHttp()
    const [workers, setWorkers] = useState(null)
    const [wa, setWa] = useState(null)
    const [date_from, setDate_from] = useState(null)
    const [date_to, setDate_to] = useState(null)
    const [dateRange, setDateRange] = useState(null)
    const [startRange, setStartRange] = useState(null)
    const [endRange, setEndRange] = useState(null)
    const [resultRange, setResultRange] = useState(null)
    const auth = useContext(AuthContext)

    useEffect( ()=> {
        async function fetchStaffandWA(){
            const workers = await request('/allstaff','POST') //        
            const allWA = await request('/allWA','POST') //        

            for ( let worker of workers ) {
                worker.workTime.map(oneWork => {
                    for(let wa of allWA){
                        if(String(oneWork)==String(wa._id)){
                            const index = worker.workTime.indexOf(oneWork)
                            if (index !== -1) {
                                worker.workTime[index] = wa
                            }
                        }
                    }
                })
            }
            setWorkers(workers)
            setWa(allWA)
            console.log('workers: ', workers)
            console.log('allWA: ', allWA)
        }
        fetchStaffandWA()
    },[])
    useEffect(()=>{
        if(date_from && date_to) {
            const range = getDaysBetweenDates(date_from,date_to)
            setDateRange(range)
        }

    },[date_from,date_to])

    const changeHandler = event => {
        if(event.target.name==='date_from'){
            setStartRange(null);setDate_from(null);
            setStartRange(event.target.valueAsDate)
            setDate_from(moment(event.target.value))
        } else if (event.target.name==='date_to') {
            setEndRange(null);setDate_to(null)
            setEndRange(event.target.valueAsDate)
            setDate_to(moment(event.target.value))
        }
    }

    const getDaysBetweenDates = (startDate, endDate) => {
        const now = startDate.clone(), dates = [];

        while (now.isSameOrBefore(endDate)) {
            dates.push(now.format('YYYY-MM-DD'));
            now.add(1, 'days');
        }
        return dates;
    };

    function buttonHandler () {
        arr = []
        console.log(dateRange)
        for (let one_date of dateRange) {
            for(let one_wa of wa){
                if (one_date === (one_wa.dogovor_date).split('T')[0]) {
                    const obj = {
                        date: one_date,
                        name: one_wa.worker_name,
                        workerID: one_wa.worker_id,
                        tips: one_wa.tips
                    }
                    arr.push(obj)
                }
            }
        }
        setResultRange(arr)
        console.log(arr)
    }

    function TableTr () {
        return(
            <>
                {
                    resultRange.map(e=>{
                        return(
                            <tr>
                                <td className="name_finance">{e.name}</td>

                                {
                                    dateRange.map(dateFromRange=>{
                                        return(
                                            <>
                                                {
                                                    e.date === dateFromRange
                                                        ?<td className="tdTips_finance">{e.tips}&nbsp;</td>
                                                        :<td className="tdTips_finance">-</td>
                                                }
                                            </>
                                        )
                                    })
                                }
                            </tr>
                        )
                    })
                }
            </>
        )
    }
    const returnTable = () => {
        return (
            <div className="radius_finance">
                <div className="">
                    <table className=" table_finance tableFinance table-hover  table-condensed table-responsive">
                        <thead>
                        <tr>
                            <th className="name_finance"></th>
                            {
                                dateRange.map(date=>{
                                    return(
                                        <th className="date_finance">{date.replace(/^.{8}/, '')}</th>
                                    )
                                })
                            }

                        </tr>
                        <tr className="razdelitel_finance"></tr>
                        </thead>

                        <tbody>

                        <TableTr/>
                        </tbody>
                    </table>
                </div>
            </div>
        )
    }
    const waitingData = () => {
        return(
            <div className="waiting_finance">
                 ...
            </div>
        )
    }
function checkDateFromAndDateTo () {
    console.log(`date_from: ${date_from}, date_to: ${date_to}`)
    console.log(`dateRange: ${dateRange}`)
}
async function testmiddleware() {
    console.log('middleware!')
    await request('/middleware','POST', null,{ 'Authorization': `Bearer ${auth.token}` }) //        
}

    return(
        <div >
            <div className="topDiv_finance">
                  
            </div>
            <div className="mainDiv_finance">
                <div className="d-inline-flex ">
                    <div>
                        :
                        <input onChange={changeHandler} name="date_from" type="date" className="form-control"/>
                    </div>
                    <div className="secondElement_funance">
                        :
                        <input onChange={changeHandler} name="date_to" type="date" className="form-control"/>
                    </div>
                </div>
                <button  className="buttonShow_finance rainbow rainbow-1" onClick={buttonHandler}></button>
            </div>
            <div className="divTable_finance">
                {resultRange? returnTable() :waitingData()}
            </div>
        </div>

    )
}
export default Finance