import React, {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import {useHttp} from "../../hooks/http.hook";
import './Schedule.css'
import {Hearts} from "react-loader-spinner";


function Dogovor(){
    const [allContract, setAllContracts]=useState(null)
    const {request} = useHttp()

    function returnTable(){
        return(
            <table className="table-hover tableDogovor">
                <thead>
                <tr>
                    <th></th>
                    <th> </th>
                    <th></th>
                    <th> </th>
                    <th></th>
                    <th> </th>
                </tr>
                </thead>
                {
                    allContract.map(e=>{
                        return(
                            <tbody>
                            <tr>
                                <td>{e.company}</td>
                                <td>{e.contactFace}</td>
                                <td>{e.address}</td>
                                <td>{e.totalSum}</td>
                                <td>{e.phone}</td>
                                <td>{e.additional}</td>
                            </tr>
                            </tbody>
                        )
                    })
                }
            </table>
        )
    }

    useEffect(() => {
        async function fetchContracts(){
            const contracts = await request('/contracts','POST')
            setAllContracts(contracts)
        }
        fetchContracts()
    },[])

    return(

        <div className="mainBox">
            <div className="linkUnderNavbar">
                <Link to="/dogovor/new">
                    <button className="rainbow rainbow-1"> </button>
                </Link>
            </div>

            {!allContract?
                <Hearts
                    className="loaderBoxFc"
                    type="Puff"
                    color="#00BFFF"
                    height={45}
                    width={45}
                />
                :returnTable()
            }
        </div>
    )
}
export default Dogovor